
import Vue from 'vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import { getImgDetail, submitImgComment,submitImgZan } from '@/api/detail'
export default {
name: 'shoppingCard',
components: {
Radio,
Group,
Swiper,
SwiperItem
},
data () {
return {
detail:'',
id:'',
commlist:[],
userinput:'',
bannerlist:[],
commitnum:10,
share:false,
actid:''
}
},
created () {
this.id = this.$route.query.id
this.actid = this.$route.query.actid
this.getlist()
},
methods: {
getlist(){
const postData = {
id:this.id
}
getImgDetail(postData)
.then(res => {
if (res.data.errorcode === 0) {
this.detail = res.data.data
this.commlist = res.data.data.commlist
let imglist = {}




this.bannerlist = res.data.data.piclist
this.getshare()
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('请求出错！', 'middle')
})
},
goallworks(){
this.$router.push({path:"/showphoto/allworks",query:{"id":this.actid}})
},
zan(id){
const postData = {
id:this.id
}
submitImgZan(postData)
.then(res => {
if (res.data.errorcode === 0) {
this.$vux.toast.text("点赞成功", 'middle')
this.detail.selfzan=1
this.detail.zannum = this.detail.zannum*1+1
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('请求出错！', 'middle')
})
},
send(){
if(this.userinput==''){
this.$vux.toast.text('请输入评论内容', 'middle')
return
}
const postData = {
id:this.id,
content:this.userinput
}
submitImgComment(postData)
.then(res => {
if (res.data.errorcode === 0) {
this.$vux.toast.text('提交成功', 'middle')
this.userinput = ''
setTimeout(()=>{
location.reload()
},1000)
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('请求出错！', 'middle')
})
},
shareeject(){
this.share = true
},
closeeject(){
this.share = false
},
morecommit(){
if(this.commitnum>this.commlist.length){
this.$vux.toast.text('已显示全部评论', 'middle')
return
}
this.commitnum = this.commitnum*1+10
},
previewset (index) {
const that= this
that.$wechat.previewImage({
current: that.bannerlist[index], // 当前显示图片的http链接
urls: that.bannerlist 
});
},
getshare(){
const that = this
const usertitle = that.detail.title
const subtitle = that.detail.subtitle
const shareimg = that.detail.pic
Vue.wechat.ready(() => {
Vue.wechat.checkJsApi({
jsApiList: ['chooseImage', 'previewImage', 'uploadImage', 'downloadImage', 'onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'scanQRCode'], // 需要检测的JS接口列表，所有JS接口列表见附录2,
success: function (res) {
console.log(res)
}
})

const shareid = localStorage.getItem('shareid')

Vue.wechat.onMenuShareTimeline({
title: usertitle,
link: process.env.BASE_SELF+'/weixin/index.html?#/showphoto/workdetail?id='+that.id+'',
imgUrl: shareimg,

success: function (res) {

}
})

Vue.wechat.onMenuShareAppMessage({
title: usertitle,
desc: subtitle,
link: process.env.BASE_SELF+'/weixin/index.html?#/showphoto/workdetail?id='+that.id+'',
imgUrl: shareimg,

success: function (res) {

}
})
})
}
},
mounted(){

}
}



